import React from 'react';
import _ from "lodash";

import TaskMarkView from '../views/TaskMarkView';

const getTaskStatus = (mark, taskStatus) => {
  let status = taskStatus;
  if (mark !== '') {
    status = 'checked';
  } else if (mark === '' && taskStatus === 'checked') {
    status = 'checking-time-gone';
  }
  return status;
};

const TasksCreator = ({ link, name, status, students }) => (
  <tr>
    <td><a href={link} target="_blank" rel="noopener noreferrer">{name}</a></td>
    {
      _.map(students, (student) => {
        const { mark } = _.find(student.tasks, { name });
        const taskStatus = getTaskStatus(mark, status.toLowerCase().replace(/\s/, '-'));

        return (
          <TaskMarkView key={`${name}-${student.github}`} mark={mark} status={taskStatus}/>
        );
      })
    }
  </tr>
);

export default TasksCreator;
