import React from 'react';

import StudentsView from '../views/StudentsView';

const Students = (props) => {
  const { mentor } = props;
  const { students } = mentor;
  return (
    <thead>
      <tr>
        <th>task \ student</th>
        {
          students.map(student => <StudentsView key={student.github} {...student}/>)
        }
      </tr>
    </thead>
  );
};

export default Students;
