import React, { Component } from 'react';
import Select from 'react-select';
import _ from 'lodash';

import mentors from './json/mentors.json';
import Students from './components/Students';
import Tasks from './components/Tasks';
import Notes from './views/Notes';

const options = _.map(mentors, mentor => ({ value: mentor.github, label: mentor.github }));

class Dashboard extends Component {
  state = {
    mentor: JSON.parse(localStorage.getItem('mentor')) || '',
  };

  handleChange = (selectedMentor) => {
    const currentMentor = _.find(mentors, { github: selectedMentor.value });
    if (currentMentor) {
      this.setState({
        mentor: currentMentor,
      });
      localStorage.setItem('mentor', JSON.stringify(currentMentor));
    }
  };

  async componentDidMount() {
    const res = await fetch('/me');
    const user = await res.text();
    const currentMentor = _.find(mentors, { github: user });
    if (currentMentor) {
      this.setState({
        mentor: currentMentor,
      });
      localStorage.setItem('mentor', JSON.stringify(currentMentor));
    }
  }

  render() {
    const { mentor } = this.state;
    if (!mentor) {
      return (
        <section>
          <a href="/auth/github" className="github-auth"><i className="fa fa-github fa-2x" aria-hidden="true"> Github auth</i></a>
          <Select
            className="container mentor-select"
            onChange={this.handleChange}
            options={options}
          />
        </section>
      );
    }
    return (
      <section>
        <a href="/auth/github" className="github-auth"><i className="fa fa-github fa-2x" aria-hidden="true"> Github auth</i></a>
        <Select
          className="container mentor-select"
          dafaultInputValud={mentor.github}
          onChange={this.handleChange}
          options={options}
        />

        <table className="dashboard-table">
          <caption>{mentor.github}</caption>

          <Students mentor={mentor} />
          <Tasks {...mentor} />
          <Notes />
        </table>
      </section>
    );
  }
}

export default Dashboard;
