import _ from 'lodash';
import React from 'react';

import TasksCreator from './TasksCreator';

const Tasks = ({ students }) => {
  const { tasks } = students[0];

  return (
    <tbody>
      {
        _.map(tasks, task => <TasksCreator key={task.link} {...task} students={students}/>)
      }
    </tbody>
  );
};

export default Tasks;
